.tabs {
    font-family: inherit;
    margin: 0 auto;
    width: 70%;
    text-align: center;
    margin-top: 15vh;
}

button:focus {
    outline: none;
}

.tab-buttons {
    margin-bottom: 30px;
}

.tab-buttons button {
    color: rgb(189, 197, 223);
}

button {
    margin-right: 15px;
    background: transparent;
    border: none;
    outline: none;
    padding: 10px 15px;
    cursor: pointer;
    color: rgba(0, 0, 0, .4);
    transition: all ease-in-out .2s;
    border-bottom: 2px solid transparent;
}

.active {
    border-bottom: 2px solid #42b3f4;
}